.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-logo {
  height: 40vmin;
}

.App-header {

}

.App-link {
  color: #09D3AC;
}

.b-list {
  display: block;
  list-style: none;

  &__list{
    display: inline-block;
  }

  &__link {
    color: #09D3AC;
    padding: 10px;
    display: inline-block;
    text-decoration: none;
    border: 4px solid;
    margin: 10px;
  }
}
